import { Ignite } from '@mornya/react-helper';
var option = {
    application: {
        component: function () { return import('./App'); },
        rootElementId: 'app',
        onHotReload: function (next) { return module.hot && module.hot.accept('./App', next); },
    },
    router: {
        isUseBrowserRouter: true,
        basename: process.env.PUBLIC_URL,
    },
    serviceWorker: {
        isUse: process.env.NODE_ENV === 'production',
        config: {
            url: './',
            file: 'service-worker.js',
        },
    },
};
Ignite.init(option);
